import React, { Component, Fragment } from 'react'
import styles from './styles';

//material
import { withStyles } from '@material-ui/core';
import { ThemeProvider } from '@material-ui/core/styles';
import theme from '../../commons/Theme';
import {Provider} from 'react-redux';
import configureStore from './../../redux/configureStore';
import CommonModal from '../../Components/Modal';
import {ADMIN_ROUTES , ROUTES} from '../../constants/index';
import AdminLayoutRoute from '../../commons/Layout/AdminLayoutRoute';

//helpers 
import { ToastContainer } from 'react-toastify';
import GlobalLoading from '../../Components/GlobalLoading';

//router 
import {BrowserRouter, Switch} from 'react-router-dom';
import CssBaseline from '@material-ui/core/CssBaseline';
import DefaultLayoutRoute from '../../commons/Layout/DefaultLayoutRoute';

const store = configureStore();

class App extends Component {

    renderAdminRoutes = () => {
        let xhtml = null;
            xhtml = ADMIN_ROUTES.map(route => {
                return (
                    <AdminLayoutRoute key={route.path} component={route.component} exact={route.exact} path={route.path} name={route.name} />
                )
            })
        return xhtml;
    }

    renderDefaultRoutes = () => {
        let xhtml = null;
            xhtml = ROUTES.map(route => {
                return (
                    <DefaultLayoutRoute key={route.path} component={route.component} exact={route.exact} path={route.path} name={route.name} />
                )
            })
        return xhtml;
    }

    render() { 
        return ( 
            <Provider store={store}>
                <BrowserRouter>
                    <Fragment>

                        <ThemeProvider theme={theme}>
                            <CssBaseline />
                            <GlobalLoading />
                            <ToastContainer/>
                            <CommonModal />
                            <Switch>
                                {this.renderAdminRoutes()}
                                {this.renderDefaultRoutes()}
                            </Switch>
                        </ThemeProvider>
                    </Fragment>
                </BrowserRouter>
            </Provider>
            

        );
    }
}
 
export default withStyles(styles)(App);



