import React, { Component } from "react";
import PropTypes from "prop-types";
import {connect} from "react-redux";
import {compose, bindActionCreators} from "redux";
import { Field, reduxForm } from 'redux-form'

//Material
import { withStyles, Button, Grid, Box } from "@material-ui/core";
import MenuItem from '@material-ui/core/MenuItem';

//local
import styles from "./style";
import * as modalActions from '../../actions/modal';
import * as taskActions from '../../actions/task';
import renderTextField from "../../Components/FormHelper/TextField";
import renderSelectField from '../../Components/FormHelper/Select';
import validate from './validate';

class TaskForm extends Component {
    handleSubmitForm = data => {
        const { taskActionsCreators, taskEditing} = this.props;
        const { addTask, updateTask } = taskActionsCreators;
        const { title, description, status } = data;

        if(taskEditing && taskEditing.id) {
            updateTask(title,description,status)
        }else {
            addTask(title, description);
        }
        
        
    }

    renderStatusSelection = () => {
        let xhtml = null;
            const{taskEditing, classes} = this.props;
            if(taskEditing && taskEditing.id){
                xhtml = (
                <Field
                    id="status"
                    name="status"
                    label="Trạng Thái"
                    className={classes.select}
                    component={renderSelectField}
                >
                <MenuItem value={0}>Ready </MenuItem>
                <MenuItem value={1}>IN PROGRESS</MenuItem>
                <MenuItem value={2}>COMPLETED</MenuItem>

                
                </Field>
                
                )
            }
        return xhtml;
    }

    render() {
        const { classes , modalActionsCreators, handleSubmit, invalid ,submitting } = this.props;
        const {hideModal} = modalActionsCreators;
        
        return (
            <form className={classes.form} onSubmit={handleSubmit(this.handleSubmitForm)}>
                <Grid container spacing={8}>
                    <Grid item md={12}>
                        <Field
                            id="title"
                            name="title"
                            label="Tiêu Đề"
                            className={classes.textField}
                            margin="normal"
                            component={renderTextField}
                        />
                        
                    </Grid>

                    <Grid item md={12}>
                        <Field
                            id="description"
                            name="description"
                            label="Mô Tả"
                            multiline
                            rowsMax="4"
                            className={classes.textField}
                            margin="normal"
                            component={renderTextField}
                        />
                    </Grid>

                    <Grid item md={12}>
                        {this.renderStatusSelection()}
                    </Grid>

                    <Grid item md={12}>
                        <Box display="flex" flexDirection="row-reverse" mt={2}>
                            <Button variant="contained" onClick={hideModal}>Can</Button>

                            <Box mr={2}>
                                <Button disabled = {invalid || submitting} variant="contained" color="primary" type="submit">
                                    Ok
                                </Button>
                            </Box>
                        </Box>
                    </Grid>
                </Grid>
            </form>
        );
    }
}

TaskForm.propTypes = {
    classes: PropTypes.object,
    modalActionsCreators: PropTypes.shape({
        hideModal: PropTypes.func,
    }),
    taskActionCreators: PropTypes.shape({
        addTask: PropTypes.func,
    }),
    handleSubmit: PropTypes.func,
    invalid : PropTypes.bool,
    submitting : PropTypes.bool,
};

const mapState = state => {
    return {
        taskEditing: state.task.taskEditing,
        initialValues : {
            title : state.task.taskEditing ? state.task.taskEditing.title : '',
            description: state.task.taskEditing ? state.task.taskEditing.description : '',
        }
    }
}


const mapDispatch = dispatch => {
    return {
        modalActionsCreators : bindActionCreators(modalActions, dispatch),
        taskActionsCreators: bindActionCreators(taskActions, dispatch)
    }
}

const withConnect = connect(mapState , mapDispatch);

const FORM_NAME = 'FORM_NAME';

const withReduxForm = reduxForm({
    form: FORM_NAME,
    validate,
})


export default compose(
    withStyles(styles),
    withConnect,
    withReduxForm,
)(TaskForm);
