import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { compose, bindActionCreators} from "redux";

//Material
import { withStyles } from "@material-ui/core";
import Modal from "@material-ui/core/Modal";
import CloseIcon from '@material-ui/icons/Clear';


//local
import styles from "./style";
import * as modalActions from "../../actions/modal";

class CommonModal extends Component {
    render() {
        const { classes, open, component, title , modalActionsCreators } = this.props;
        const {hideModal} = modalActionsCreators;
        return (
            <Modal open={open} onClose={hideModal}>
                <div className={classes.modal}>
                    <div className={classes.header}>
                        <span className={classes.title}>{title}</span>
                        <CloseIcon className={classes.icon} onClick={hideModal}/>
                    </div>
                    {component}
               
                </div>
            </Modal>
        );
    }
}

CommonModal.propTypes = {
    classes: PropTypes.object,
    open: PropTypes.bool,
    modalActionsCreators: PropTypes.shape({
        hideModal: PropTypes.func,
    }),
};

const mapState = state => {
    return {
        open: state.modal.showModal,
        component: state.modal.component,
        title: state.modal.title,
    }

};

const mapDispatch = dispatch => {
    return {
        modalActionsCreators : bindActionCreators(modalActions, dispatch),
    }
}

const withConnect = connect(mapState , mapDispatch);

export default compose(
    withStyles(styles),
    withConnect,
)(CommonModal);
