import React, { Component } from "react";
import PropTypes from "prop-types";

//Material
import { withStyles, Box } from "@material-ui/core";
import Button from "@material-ui/core/Button";
import AddIcon from "@material-ui/icons/Add";
import Grid from "@material-ui/core/Grid";

//Internal
import styles from "./styles";
import { STATUSES } from "../../constants/index";
import TaskList from "../../Components/TaskList";
import TaskForm from "../TaskForm";
import * as taskActions from "../../actions/task";
import * as modalActions from "../../actions/modal";

//redux
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import SearchBox from "../../Components/SearchBox";

class Taskboard extends Component {
    componentDidMount() {
        const { taskActionCreators } = this.props;
        const { fetchListTask } = taskActionCreators;
        fetchListTask();
    }

    handleEditTask = (task) => {
        const { taskActionCreators, modalActionsCreators } = this.props;
        const { setTaskEditing } = taskActionCreators;
        const {
            showModal,
            changeModalTitle,
            changeModalContent,
        } = modalActionsCreators;
        setTaskEditing(task);
        showModal();
        changeModalTitle("Cập nhật công việc");
        changeModalContent(<TaskForm />);
    };

    showModalDeleteTask = (task) => {
        const {
            modalActionsCreators,
            classes,
        } = this.props;
        const {
            showModal,
            hideModal,
            changeModalTitle,
            changeModalContent,
        } = modalActionsCreators;
        showModal();
        changeModalTitle("Xóa công việc");
        changeModalContent(
            <div className={classes.modalDelete}>
                <div className={classes.modalConfirmText}>
                    Bạn có muốn xóa{" "}
                    <span className={classes.modalConfirmTextBold}>
                        {task.title}
                    </span>
                </div>
                <Box display="flex" flexDirection="row-reverse" mt={2}>
                    <Box>
                        <Button variant="contained" onClick={hideModal}>
                            Hủy Bỏ
                        </Button>
                    </Box>
                    <Box>
                        <Button
                            variant="contained"
                            color="primary"
                            onClick={() => this.handleDeleteTask(task)}
                        >
                            Đồng ý
                        </Button>
                    </Box>
                </Box>
            </div>
        );
    };

    handleDeleteTask(task) {
        const {id} = task;
        const {taskActionCreators} = this.props;
        const {deleteTask} = taskActionCreators;
        deleteTask(id)
    }

    openForm = () => {
        const { modalActionsCreators, taskActionCreators } = this.props;
        const {
            showModal,
            changeModalTitle,
            changeModalContent,
        } = modalActionsCreators;
        const { setTaskEditing } = taskActionCreators;
        setTaskEditing(null);

        showModal();
        changeModalTitle("Thêm Mới Công Việc");
        changeModalContent(<TaskForm />);
    };

    loadData = () => {
        const { taskActionCreators } = this.props;
        const { fetchListTask } = taskActionCreators;
        fetchListTask();
    };

    handleFilter = (e) => {
        const { value } = e.target;
        const { taskActionCreators } = this.props;
        const { filterTask } = taskActionCreators;
        filterTask(value);
    };

    renderSearchBox() {
        let xhtml = null;
        xhtml = <SearchBox handleChange={this.handleFilter} />;
        return xhtml;
    }

    renderBoard() {
        const { listTask } = this.props;
        let xhtml = null;
        xhtml = (
            <Grid container spacing={2}>
                {STATUSES.map((status) => {
                    const taskFiltered = listTask.filter(
                        (task) => task.status === status.value
                    );
                    return (
                        <TaskList
                            task={taskFiltered}
                            status={status}
                            key={status.value}
                            onClickEdit={this.handleEditTask}
                            onClickDelete={this.showModalDeleteTask}
                        />
                    );
                })}
            </Grid>
        );
        return xhtml;
    }

    render() {
        const { classes } = this.props;
        return (
            <React.Fragment>
                <div className={classes.taskBoard}>
                    <Button
                        variant="contained"
                        color="primary"
                        className={classes.button}
                        onClick={this.openForm}
                    >
                        <AddIcon /> Thêm Công Việc
                    </Button>
                    <Button
                        variant="contained"
                        color="primary"
                        className={classes.button}
                        onClick={this.loadData}
                        style={{ marginLeft: 20 }}
                    >
                        <AddIcon /> Load
                    </Button>
                    {this.renderSearchBox()}
                    {this.renderBoard()}
                </div>
            </React.Fragment>
        );
    }
}

Taskboard.propTypes = {
    classes: PropTypes.object,
    taskActionCreators: PropTypes.shape({
        fetchListTaskRequest: PropTypes.func,
    }),
    listTask: PropTypes.array,
    modalActionsCreators: PropTypes.shape({
        showModal: PropTypes.func,
        hideModal: PropTypes.func,
        changeModalTitle: PropTypes.func,
        changeModalContent: PropTypes.func,
    }),
};

const mapStateToProps = (state) => {
    return {
        listTask: state.task.listTask,
    };
};
const mapDispatchToProps = (dispatch) => {
    return {
        taskActionCreators: bindActionCreators(taskActions, dispatch),
        modalActionsCreators: bindActionCreators(modalActions, dispatch),
    };
};

export default withStyles(styles)(
    connect(mapStateToProps, mapDispatchToProps)(Taskboard)
);
