import React, { Component } from "react";

//Material
import { withStyles } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import Card from "@material-ui/core/Card";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";

import Fab from "@material-ui/core/Fab";
import Icon from "@material-ui/core/icon";
import DeleteIcon from "@material-ui/icons/Delete";

//local
import styles from "./style";

class TaskItem extends Component {
    state = {};
    render() {
        const { classes, task, status, onClickEdit, onClickDelete } = this.props;
        const { title, id } = task;
        return (
            <Card key={id} className={classes.card}>
                <CardContent>
                    <Grid container justify="space-between">
                        <Grid item md={8}>
                            <Typography component="h2">{title}</Typography>
                        </Grid>
                        <Grid item md={4}>
                            {status.label}
                        </Grid>
                        <p>{task.description}</p>
                    </Grid>
                </CardContent>
                <CardActions className={classes.cardActions}>
                    <Fab color="primary" className={classes.fab} size="small" onClick={onClickEdit}>
                        <Icon fontSize="small">edit_icon</Icon>
                    </Fab>
                    <Fab color="primary" className={classes.fab} size="small" onClick={onClickDelete}>
                        <DeleteIcon />
                    </Fab>
                </CardActions>
            </Card>
        );
    }
}

export default withStyles(styles)(TaskItem);
